.slides {
  display: flex;
  flex-direction: row;
  align-items: center;
  text-align: center;
  margin-bottom: 2.5rem;
  max-height: 25rem;

  .slide {
    width: 80vw;

    .image {
      position: relative;
      border-radius: 1rem;
      filter: drop-shadow(0 0 0.8rem #a5a498);
    }
  }

  .switcher {
    border: none;
    position: sticky;
    font-size: 2rem;
    margin: 0 2vw 0 2vw;
    cursor: pointer;
    border-radius: 2rem;
    text-align: center;
    width: 3rem;
    line-height: 1rem;

    &:hover {
      border: none;
      border-radius: 3rem;
      border-color: #f3e1e1;
      background: #dbdbcd;
      filter: drop-shadow(0 0 0.1rem rgb(90, 84, 84));
    }

    .prev {
      left: 2vw;
    }

    .next {
      right: 2vw;
    }

    &:active {
      background-color: rgb(255, 238, 142);
    }
  }
}
.slides {
  display: flex;
