  align-items: center;
  text-align: center;
  margin-bottom: 2.5rem;
  max-height: 25rem;
  position: relative;

  .slide {
    width: 60vw;

    .image {
      position: relative;
      max-height: 25rem;
      border-radius: 1rem;
      filter: drop-shadow(0 0 0.8rem #a5a498);
      cursor: pointer;

      &:hover {
        filter: drop-shadow(0 0 0.8rem #a5a498);
      }
    }
  }

  .switcher {
    border: none;
    position: sticky;
    font-size: 2rem;
    margin: 0 2vw 0 2vw;
    cursor: pointer;
    border-radius: 2rem;
    text-align: center;
    width: 3rem;
    line-height: 1rem;

    &:hover {
      border: none;
      border-radius: 3rem;
      border-color: #f3e1e1;
      background: #dbdbcd;
      filter: drop-shadow(0 0 0.1rem rgb(90, 84, 84));
    }

    .prev {
