    }
  }
}

.modal {
  position: fixed;
  margin-top: 8rem;
  width: 100vw;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 4;

  .content {
    position: relative;

    .image {
      height: 79vh;
      object-fit: contain;
      border-radius: 1rem;
      cursor: pointer;
    }

    .close {
      position: fixed;
      top: 8rem;
      font-size: 2rem;
      color: #747275;
      background: transparent;
      border: none;
      cursor: pointer;

      &:hover {
        color: #f3e1e1;
      }
    }
  }
}
