@import url("https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");

.welcome {
  display: flex;
  flex-direction: column;
  margin-top: 8rem;
  font-family: "Poppins";
  font-weight: 700;
  text-align: center;

  .title {
    margin-top: 3rem;
    padding: 0.5rem;
    font-size: 1.5rem;
    color: rgb(243, 216, 96);
    background: linear-gradient(90deg, #858179, #727257);
    filter: drop-shadow(0 0 0.8rem #a5a498);
  }

  .text {
    font-size: 1rem;
    margin: 2rem;
  }

  .subtext {
    text-decoration: underline;
    font-size: 1.6rem;
  }

  .gym {
    width: 100vw;
    padding: 1.5rem;
    background: #fcb83c;
  }
}
