@import url("https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");

.timetable {
  font-family: "Poppins";
  font-weight: 700;


  .plan {
    text-align: center;
    font-size: 2rem;
    color: #ebe8f3;
    background: linear-gradient(30deg,  #55525a, #c4b4ca);
    padding: 1rem;
    border-top-left-radius: 2rem;
    border-bottom-right-radius: 2rem;
  }

  .table { 
    background: linear-gradient(30deg, #c4b4ca, #55525a);

    .head {

      .title {
        color: #f7f7f8;
        font-weight: bold;

        .block {
          padding: 1rem;
          text-align: center;
        }
      }
    }

    .body {
      background-color: #ebe8f3;
      font-family: "Poppins";
      font-weight: 700;

      .service {
        text-align: center;

        .category {
          color: #f58617;
          padding: 0.7rem;
        }

        .price {
        }

        .description {
          color: #f58617;
          font-size: 1rem;
          width: 40vw;
          padding: 0.5rem;
          
        }
      }
    }
  }
}
