@import url("https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");

* {
  box-sizing: border-box;

  body {
    margin: 0;
    padding: 0;
    overflow-x: hidden;

    header {
      position: fixed;
      background: linear-gradient(#aca6a6, #6b6a69);
      display: flex;
      flex-direction: column;
      align-items: center;
      top: 0;
      left: 0;
      right: 0;
      z-index: 2;

      .contact {
        position: absolute;
        width: 35vw;
        top: 1.3rem;
        left: 0.5rem;
        padding: 0.5rem;
        line-height: 1.5rem;

        .text {
          font-family: poppins;
          font-weight: 700;
          letter-spacing: 0.03rem;
          text-decoration: none;
          font-size: 0.8rem;
          color: #f7f6f3;

          &:hover {
            color: #5e5e5a;
            border-top-left-radius: 0.3rem;
            border-bottom-right-radius: 0.3rem;
            background: linear-gradient(#f3fa8d, #f58617);
            padding: 0.2rem;
          }
        }
      }

      .logo {
        position: relative;
        height: 6rem;
        margin: 1rem;
        filter: drop-shadow(0 0rem 0.1rem #5e5e5a);
      }

      .social {
        position: absolute;
        display: flex;
        flex-direction: column;
        right: 2vw;
        margin: 1rem;
        top: -0.4rem;

        .link {
          color: #faebd7;
          font-size: 2rem;

          &:hover {
            .facebook {
              color: #1676ef;
              filter: drop-shadow(0rem 0rem 0.1rem #4b95ef);
            }

            .instagram {
              color: #ce2e94;
              filter: drop-shadow(0.15rem -0.15rem 0.1rem #653bc5)
                drop-shadow(-0.15rem 0.15rem 0.1rem #fd9a19);
            }

            .tiktok {
              color: #150a18;
              filter: drop-shadow(0.15rem 0.15rem 0rem #ff1f50)
                drop-shadow(-0.15rem -0.15rem 0rem #1df2ee);
            }
          }
        }
      }
    }

    main {
      position: relative;
      z-index: 0;
      display: flex;
      flex-direction: column;
      align-items: center;
    }

    footer {
      font-family: "Poppins";
      font-weight: 700;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      position: relative;
   

      .hours {
        text-align: center;
        padding: 1rem;
        background: linear-gradient(#acaca6, #f7f6f3);

        .subtitle {
          font-size: 1.2rem;
          color: #080747;
        }
        .subtext {
          font-size: 1rem;
        }
      }

      .info {
        text-align: center;
        padding: 1rem;
        background: linear-gradient(#f7f6f3, #acaca6);

        .title {
          font-size: 1.2rem;
          color: #080747;
        }

        .text {
          font-size: 1rem;
        }
      }

      .copyright {
        font-weight: 700;
        text-align: center;
      }
    }
  }
}
